.calendar-container {
  position: relative;
  width: 100vw;           /* Viewport width */
  height: auto;
  max-width: 100%;
  margin: 0 auto;
  padding-top: 76.88%;  
}

.calendar-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}


.day-region {
  position: absolute;
  background: rgba(255, 0, 0, 0.2);
  border: 1px dashed red;
  cursor: pointer;
  color: white;
  font-size: 1rem;
  font-weight: bold;
  text-align: center;
  line-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: auto;
}

.day-region:hover {
  background: rgba(255, 255, 255, 0.3);
  outline: 2px solid yellow;
}


